.Modal_backdrop__29fHH {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    z-index: 200;
    background-color: rgba(0, 0, 0, 0.75);
  }
  
  .Modal_modal__3tsUe {
    position: fixed;
    top: 20vh;
    left: 5%;
    width: 90%;
    background-color: white;
    padding: 1rem;
    border-radius: 14px;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.25);
    z-index: 300;
    -webkit-animation: Modal_slide-down__eX0Tx 300ms ease-out forwards;
            animation: Modal_slide-down__eX0Tx 300ms ease-out forwards;
  }
  
  @media (min-width: 768px) {
    .Modal_modal__3tsUe {
      width: 40rem;
      left: calc(50% - 20rem);
    }
  }
  
  @-webkit-keyframes Modal_slide-down__eX0Tx {
    from {
      opacity: 0;
      -webkit-transform: translateY(-3rem);
              transform: translateY(-3rem);
